import React  from 'react';
import styled from 'styled-components'
import theme from '@style/theme'
import { Grid2_3 } from '@style/global'
import DayPicker from 'react-day-picker';
import 'react-day-picker/lib/style.css';

export default class Calendar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDay: null,
      time: null
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleDayClick = this.handleDayClick.bind(this);
  }

  handleDayClick(day, { selected }) {
    this.setState({
      selectedDay: selected ? undefined : day,
    });
  }

  handleChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    
    this.setState(
      {
        [name]: value
      }
    );
    this.props.parentCallback(this.state);
    event.preventDefault();
  }

  render() {
    return (
      <InputItem>
          <input type="text" name="date" value={this.state.selectedDay
              ? this.state.selectedDay.toLocaleDateString()
              : 'Please select a day'}/>
          <DayPicker
            selectedDays={this.state.selectedDay}
            onDayClick={this.handleDayClick}
          />
          <Grid2_3>
            Start at
            <select name="time" onBlur={this.handleChange} value={this.state.time}>
              <option value="13:00">12:30</option>
              <option value="13:00">13:00</option>
              <option value="13:30">13:30</option>
              <option value="14:00">14:00</option>
              <option value="14:30">14:30</option>
            </select>
          </Grid2_3>
      </InputItem>
    );
  }
}


const InputItem = styled.div`
  position:relative;
  background: ${theme.color.white};
  max-width: 300px;
  ${Grid2_3}{
    background: ${theme.color.grayscale.S100};
    padding-left: 20px;
    border-top: 1px solid ${theme.color.grayscale.S300} !important; 
    border-bottom: 1px solid ${theme.color.grayscale.S500} !important;
    align-items: center;
  }
  select{
    height: 30px;
  }
  input{ 
    border-bottom: 1px solid ${theme.color.grayscale.S500}; 
    ${theme.fontSize.sm};
    color: ${theme.color.grayscale.S1000};
    padding: 0 10px;
    ::placeholder{
      ${theme.fontSize.sm};
    }
    height:30px;
  }
  input:focus{ 
    border-bottom:1px solid ${theme.color.primary.S400};
  }
  .DayPicker-Caption{
    ${theme.fontSize.sm};
  }
  .DayPicker-Weekdays, .DayPicker-Body {
    ${theme.fontSize.xs};
    
  }
`